body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html, body {
    height: 100%;
    background: lightskyblue;
}

body .row {
    margin-right: 0;
    margin-left: 0;
}


.vertical-center {
    min-height: 100%;  /* Fallback for vh unit */
    min-height: 100vh; /* You might also want to use
                        'height' property instead.
                        
                        Note that for percentage values of
                        'height' or 'min-height' properties,
                        the 'height' of the parent element
                        should be specified explicitly.
  
                        In this case the parent of '.vertical-center'
                        is the <body> element */

    /* Make it a flex container */
    display: flex;

    /* Align the bootstrap's container vertically */
    align-items : center;

    /* In legacy web browsers such as Firefox 9
       we need to specify the width of the flex container */
    width: 100%;

    /* Also 'margin: 0 auto' doesn't have any effect on flex items in such web browsers
       hence the bootstrap's container won't be aligned to the center anymore.

       Therefore, we should use the following declarations to get it centered again */
    justify-content : center;
}

.container {
    background-color: #eeeeee;
    padding-left: 0;
    padding-right: 0;
    max-width: 750px;
    min-width: 600px;
    width: 750px;
    height: 700px;
}

.hidden { display:none; }

.screen {
    background-color: #fff;
    max-width: 750px;
    min-width: 600px;
    width: 750px;
    height: 700px;
}

.list {
    background-color: #fff;
    overflow-y: auto;
    height: 70%;
}



div.Header {
    background: #0E1D3C;
    width: 100%;
    color: #fff;
    text-align: left;
    height: 120px
}

.Header {
    display: flex;
    align-items: center;
    padding: 0.875rem;
    overflow: hidden;
    text-overflow: ellipsis;
}

.Header-img_icon {
    cursor:pointer;
    margin: 5px 12px;
    height: auto;
    width: 50px;
}

.Header-img_icon:hover {
    -webkit-filter: drop-shadow(12px 12px 21px #555);
    filter: drop-shadow(12px 12px 29px #555);
    -webkit-transition: all 100ms ease-in;
    -webkit-transform: scale(1.1);
    -ms-transition: all 100ms ease-in;
    -ms-transform: scale(1.1);
    -moz-transition: all 100ms ease-in;
    -moz-transform: scale(1.1);
    transition: all 100ms ease-in;
    transform: scale(1.1);
}


.Header-top__title {
    width: 100%;
    box-sizing: border-box;
    padding-right: 1rem;
    overflow: hidden;
}

.Header-top__title h2 {
    font-size: 2.6rem;
    margin: 0 0 0.125rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Header .Header-img__place.postList {
    display:none;
}

h2.postList {
    font-size: 4.2rem;
    margin-left: 20px;
}

span.postList {
    font-size: 2.3rem;
    margin-left: 20px;
    font-weight: 400;
    opacity: ;
}

.Header .Header-img__place.commentsList, .Header .Header-img__place.postAdd {
    display:block;
}

h2.commentsList, h2.postAdd {
    font-size: 2.6rem;
    margin: 0 0 0.125rem;
    overflow: hidden;
    text-overflow: clip;
    white-space: normal;
}

span.commentsList,span.postAdd {
    display:none;
}

.Avatar {
    margin: 4em 1em;
    position: relative;
    display: inline-block;
}
.Avatar:after {
    content: '';
    position: absolute;
    right: -.3rem;
    bottom: 0rem;
    width: 2rem;
    height: 2rem;
    border-radius: 93%;
    border: 1px solid #fff;
}
.Avatar img {
    border-radius: 60%;
}

.Avatar--success:after {
    background: -webkit-linear-gradient(top, #71c100, #518d44);
}

.Avatar--success:hover:after  {
    background: -webkit-linear-gradient(top, red, #cc0000);
}



.Post {
    padding-left: 25px;
    font-weight: 700;
}

.Post .input-group {
    position: relative;
    display: inline;

}

.Post .col-xs-1,.Post .col-xs-11 {
    padding-right: 0;
    padding-left: 5px;
}


.Post:last-child {
    margin-bottom: 150px
}

.Post__:last-child {
    margin-bottom: 200px
}

.Post__commentsCount {
    font-weight: 400;
    position: relative;
    left: 25px;
    padding: 6px 16px;
    font-size: 24px;
    color: #fff;
    background: #0E1D3C;
    border: 0;
    overflow: hidden;
    border-radius: 30px;
    display: inline-block;
    vertical-align: middle;
}

.Post__commentsCount:hover {
    -webkit-filter: drop-shadow(2px 2px 10px rgb(185, 225, 250));
    filter: drop-shadow(2px 2px 11px rgb(185, 225, 250));
    border-radius: 32px;
    padding: 8px 18px;
    left: 23px;
    transition: all 100ms ease-in;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.noselect {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

hr {
    margin-top: 10px;
    margin-bottom: 20px;
    border-top: 2px solid #eee;
}

.Post .Post__title, .Post .Post__commentsCount {
    cursor: pointer;
}


.Post__title {
    display: inline-block;
    font-size: 20px;
    white-space: nowrap;
    line-height: 82px;
    overflow: hidden;
    text-overflow: ellipsis;

}

.Post-del__place {
    opacity: 0.2;
    height: 100%;
    line-height: 26px;
    text-align: center;
    color: #000;
    border: #000 1px double;
    font-weight: 400;
}

.Post-del__place:hover{
    opacity: 1;
    color: #fff;
    background-color: #ff2e59;
    border: #fff 1px double;
}

.Post .row-mod {
    height: 82px;
}

.Post .row-mod:hover {
    box-shadow: -5px 0 0 #ff2e59;
}


.Post .row-mod [class*="col-"] {
    vertical-align: middle;
}

.Post-counter__place {
    padding-top: 27px;
}

.invisible {
    visibility: hidden;
}

.PlusButton-img_icon {
    cursor:pointer;
    margin: 5px 20px;
    height: auto;
    width: 50px;
    -webkit-filter: drop-shadow(3px 3px 3px #111);
    filter: drop-shadow(3px 3px 3px #111);
    transition:all 0.3s ease;
}

.PlusButton-img_icon:active {
    webkit-transform: scale(0.75);
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
    -webkit-filter: none;
    filter: none;
}

.PlusButton-img_icon:hover {
    webkit-transform: scale(1.15);
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}


.PlusButton-img__place {
    display: flex;
    justify-content: center; /* for horizontal alignment */
    align-items: center;     /* for vertical alignment   */
}
.gray {
    background-color: #eee;
}
.CommentsList {
    background-color: #fff;
    overflow-y: auto;
    height: 70%;
}

.CommentsList::-webkit-scrollbar {
    width: 1em;
}

.CommentsList::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.CommentsList::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
}

.hidden { display:none; }

.Comments {
    padding: 0 25px;
}

.Comments__text {
    font-size: 14px;
    line-height: 1.3;
    text-align: justify;
    padding: 5px;
}

.Comments__icon {
    padding-top:10px;
    max-width: 64px;
}

.Comments:last-child .divider {
    display:none;
}
.CommentAdd {
    margin: 20px 0;
}

.CommentAdd-add {
    width:40px;
    height:40px;
    border: 0;
    margin: 0 20px;
    cursor:pointer;
}


.CommentAdd-add:hover {
    -webkit-filter: drop-shadow(2px 2px 10px rgb(185,225,250));
    filter: drop-shadow(2px 2px 11px rgb(185,225,250));
    -webkit-transition: all 100ms ease-in;
    -webkit-transform: scale(1.1);
    -ms-transition: all 100ms ease-in;
    -ms-transform: scale(1.1);
    -moz-transition: all 100ms ease-in;
    -moz-transform: scale(1.1);
    transition: all 100ms ease-in;
    transform: scale(1.1);
}

.PostAdd {
    margin: 10px 0;
    padding: 5px;
}

.PostAdd-input {
    box-shadow: 0 2px;
    padding-bottom: 5px;
    border: none;
    background-color: #fff;
}

.PostAdd .input-group-lg>.form-control {
    border-radius: 0;
}

.PostAdd-add {
    width:40px;
    height:40px;
    border: 0;
    margin: 0 5px;
    cursor:pointer;
}

.PostAdd-add:hover {
    -webkit-filter: drop-shadow(2px 2px 10px rgb(185,225,250));
    filter: drop-shadow(2px 2px 11px rgb(185,225,250));
    -webkit-transition: all 100ms ease-in;
    -webkit-transform: scale(1.1);
    -ms-transition: all 100ms ease-in;
    -ms-transform: scale(1.1);
    -moz-transition: all 100ms ease-in;
    -moz-transform: scale(1.1);
    transition: all 100ms ease-in;
    transform: scale(1.1);
}


